* {
  box-sizing: border-box;
  font-family: 'Space Grotesk', 'Inter', sans-serif;
}

body {
  margin: 0;
  background: #020408;
  background-image: 
    radial-gradient(circle at 20% 50%, rgba(58, 12, 163, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(0, 132, 255, 0.03) 0%, transparent 50%);
  color: #b0b8d0;
  overflow-x: hidden;
  animation: backgroundShift 15s ease infinite;
}

@keyframes backgroundShift {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    linear-gradient(90deg, rgba(58, 12, 163, 0.015) 1px, transparent 1px),
    linear-gradient(rgba(58, 12, 163, 0.015) 1px, transparent 1px);
  background-size: 60px 60px;
  pointer-events: none;
  z-index: 0;
  animation: gridMove 20s linear infinite;
}

@keyframes gridMove {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(60px, 60px);
  }
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 1rem;
  position: relative;
  z-index: 1;
}

@media (max-width: 768px) {
  .app {
    padding: 0.5rem;
  }
}