.app {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  background: linear-gradient(145deg, rgba(5, 6, 10, 0.98), rgba(3, 4, 8, 0.99));
  backdrop-filter: blur(10px);
  padding: 2.5rem;
  width: 100%;
  max-width: 460px;
  border-radius: 20px;
  border: 1px solid rgba(108, 99, 255, 0.12);
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.95),
    0 0 60px rgba(58, 12, 163, 0.15),
    inset 0 1px 0 rgba(255, 255, 255, 0.02);
  animation: fadeIn 0.6s ease, cardFloat 6s ease-in-out infinite;
  position: relative;
  overflow: hidden;
}

@media (max-width: 768px) {
  .card {
    padding: 1.5rem;
    border-radius: 16px;
  }
}

@media (max-width: 480px) {
  .card {
    padding: 1.25rem;
    border-radius: 12px;
  }
}

.card::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(108, 99, 255, 0.1) 0%, transparent 70%);
  animation: rotateGlow 20s linear infinite;
  pointer-events: none;
}

.card::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(108, 99, 255, 0.1), transparent);
  animation: shimmer 8s ease-in-out infinite;
}

@keyframes cardFloat {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}

@keyframes rotateGlow {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes shimmer {
  0%, 100% {
    left: -100%;
  }
  50% {
    left: 100%;
  }
}

.center {
  text-align: center;
}

h1 {
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 700;
  font-size: 2.2rem;
  margin: 0 0 1.5rem 0;
  background: linear-gradient(135deg, #7b72ff 0%, #00bfff 50%, #7b72ff 100%);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  position: relative;
  z-index: 1;
  animation: gradientWave 4s ease infinite, textFloat 3s ease-in-out infinite;
  filter: drop-shadow(0 0 20px rgba(108, 99, 255, 0.5));
}

@media (max-width: 768px) {
  h1 {
    font-size: 1.8rem;
    margin: 0 0 1.25rem 0;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 1.5rem;
    margin: 0 0 1rem 0;
  }
}

h2 {
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 600;
  font-size: 1.5rem;
  margin: 0 0 1.5rem 0;
  color: #00bfff;
  position: relative;
  z-index: 1;
  animation: textGlow 2s ease-in-out infinite, textFloat 4s ease-in-out infinite;
}

@media (max-width: 768px) {
  h2 {
    font-size: 1.3rem;
    margin: 0 0 1.25rem 0;
  }
}

@media (max-width: 480px) {
  h2 {
    font-size: 1.15rem;
    margin: 0 0 1rem 0;
  }
}

@keyframes gradientWave {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

@keyframes textFloat {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-5px);
  }
}

@keyframes textGlow {
  0%, 100% {
    filter: drop-shadow(0 0 10px rgba(0, 191, 255, 0.5));
  }
  50% {
    filter: drop-shadow(0 0 20px rgba(0, 191, 255, 0.8));
  }
}

input {
  width: 100%;
  padding: 0.9rem 1rem;
  margin-bottom: 0.6rem;
  border-radius: 12px;
  border: 1px solid rgba(108, 99, 255, 0.15);
  background: rgba(5, 6, 12, 0.9);
  color: #b0c0e0;
  font-size: 1rem;
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  transition: all 0.3s ease;
  position: relative;
  z-index: 1;
}

@media (max-width: 480px) {
  input {
    padding: 0.75rem 0.85rem;
    font-size: 0.95rem;
  }
}

input:focus {
  outline: none;
  border-color: rgba(108, 99, 255, 0.5);
  background: rgba(15, 18, 28, 0.9);
  box-shadow: 0 0 20px rgba(108, 99, 255, 0.4);
  animation: inputGlow 2s ease-in-out infinite;
}

@keyframes inputGlow {
  0%, 100% {
    box-shadow: 0 0 20px rgba(108, 99, 255, 0.4);
  }
  50% {
    box-shadow: 0 0 30px rgba(0, 212, 255, 0.6);
  }
}

input:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

button {
  width: 100%;
  padding: 0.9rem;
  margin-bottom: 0.6rem;
  border-radius: 12px;
  border: 1px solid rgba(108, 99, 255, 0.25);
  background: linear-gradient(135deg, rgba(108, 99, 255, 0.25), rgba(0, 212, 255, 0.15));
  background-size: 200% 200%;
  color: #b0c0e0;
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 600;
  font-size: 0.95rem;
  letter-spacing: 0.5px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 10px rgba(108, 99, 255, 0.15);
  z-index: 1;
  position: relative;
  animation: borderPulse 3s ease-in-out infinite;
}

@media (max-width: 480px) {
  button {
    padding: 0.75rem;
    font-size: 0.9rem;
  }
}

@keyframes borderPulse {
  0%, 100% {
    border-color: rgba(108, 99, 255, 0.3);
    box-shadow: 0 2px 10px rgba(108, 99, 255, 0.2);
  }
  50% {
    border-color: rgba(0, 212, 255, 0.5);
    box-shadow: 0 2px 15px rgba(0, 212, 255, 0.4);
  }
}

button:hover:not(:disabled) {
  background: linear-gradient(135deg, rgba(108, 99, 255, 0.5), rgba(0, 212, 255, 0.3));
  border-color: rgba(0, 212, 255, 0.5);
  color: #fff;
  box-shadow: 0 4px 20px rgba(0, 212, 255, 0.5);
  transform: translateY(-2px) scale(1.02);
  animation: buttonGlow 1s ease-in-out infinite;
}

@keyframes buttonGlow {
  0%, 100% {
    box-shadow: 0 4px 20px rgba(0, 212, 255, 0.5);
  }
  50% {
    box-shadow: 0 6px 30px rgba(0, 212, 255, 0.7);
  }
}

button:active:not(:disabled) {
  transform: scale(0.98);
}

button.danger {
  background: linear-gradient(135deg, rgba(255, 77, 77, 0.3), rgba(255, 23, 68, 0.2));
  border-color: rgba(255, 77, 77, 0.3);
  box-shadow: 0 2px 10px rgba(255, 77, 77, 0.2);
  animation: dangerPulse 3s ease-in-out infinite;
}

@keyframes dangerPulse {
  0%, 100% {
    border-color: rgba(255, 77, 77, 0.3);
    box-shadow: 0 2px 10px rgba(255, 77, 77, 0.2);
  }
  50% {
    border-color: rgba(255, 77, 77, 0.5);
    box-shadow: 0 2px 15px rgba(255, 77, 77, 0.4);
  }
}

button.danger:hover:not(:disabled) {
  background: linear-gradient(135deg, rgba(255, 77, 77, 0.5), rgba(255, 23, 68, 0.3));
  border-color: rgba(255, 77, 77, 0.5);
  box-shadow: 0 4px 20px rgba(255, 77, 77, 0.5);
  transform: translateY(-2px) scale(1.02);
}

.list {
  margin: 1rem 0;
  max-height: 180px;
  overflow-y: auto;
  overflow-x: hidden;
  font-size: 0.85rem;
  width: 100%;
  padding-right: 0.5rem;
}

@media (max-width: 768px) {
  .list {
    max-height: 150px;
  }
}

@media (max-width: 480px) {
  .list {
    max-height: 120px;
    font-size: 0.8rem;
  }
}

.list::-webkit-scrollbar {
  width: 8px;
}

.list::-webkit-scrollbar-track {
  background: rgba(10, 12, 20, 0.8);
  border-radius: 4px;
}

.list::-webkit-scrollbar-thumb {
  background: rgba(108, 99, 255, 0.4);
  border-radius: 4px;
}

.list::-webkit-scrollbar-thumb:hover {
  background: rgba(108, 99, 255, 0.6);
}

.item {
  border-bottom: 1px solid rgba(108, 99, 255, 0.2);
  padding: 0.6rem 0.5rem 0.6rem 0;
  transition: all 0.3s ease;
  position: relative;
  z-index: 1;
  animation: itemSlideIn 0.5s ease backwards;
  word-wrap: break-word;
  overflow-wrap: break-word;
  max-width: 100%;
  font-size: 0.95rem;
  line-height: 1.5;
}

.item:nth-child(1) { animation-delay: 0.1s; }
.item:nth-child(2) { animation-delay: 0.15s; }
.item:nth-child(3) { animation-delay: 0.2s; }
.item:nth-child(4) { animation-delay: 0.25s; }
.item:nth-child(5) { animation-delay: 0.3s; }

@keyframes itemSlideIn {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.item:hover {
  padding-left: 0.5rem;
  border-left: 3px solid #6c63ff;
  background: rgba(108, 99, 255, 0.05);
  transform: translateX(5px);
  animation: itemGlow 1s ease-in-out infinite;
}

@keyframes itemGlow {
  0%, 100% {
    box-shadow: 0 0 5px rgba(108, 99, 255, 0.2);
  }
  50% {
    box-shadow: 0 0 15px rgba(108, 99, 255, 0.4);
  }
}

.item-actions {
  display: flex;
  gap: 0.3rem;
  margin-top: 0.4rem;
}

.item-actions button {
  padding: 0.4rem 0.6rem;
  font-size: 0.85rem;
  min-width: auto;
  width: auto;
  margin: 0;
  flex: 0 0 auto;
}

.feedback {
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 700;
  font-size: 1.1rem;
  min-height: 32px;
  position: relative;
  z-index: 1;
  animation: feedbackPop 0.4s ease;
}

@keyframes feedbackPop {
  0% {
    opacity: 0;
    transform: scale(0.8) translateY(-10px);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.stats {
  display: flex;
  justify-content: space-between;
  font-size: 0.9rem;
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 600;
  margin-top: 1.5rem;
  padding: 0.8rem;
  background: rgba(5, 6, 12, 0.9);
  border-radius: 8px;
  border: 1px solid rgba(108, 99, 255, 0.12);
  position: relative;
  z-index: 1;
  animation: statsSlide 0.5s ease;
}

@keyframes statsSlide {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.stats span {
  color: #00bfff;
  animation: numberPulse 2s ease-in-out infinite;
}

@keyframes numberPulse {
  0%, 100% {
    opacity: 0.8;
  }
  50% {
    opacity: 1;
  }
}

.json-controls {
  display: flex;
  gap: 0.5rem;
}

@media (max-width: 480px) {
  .json-controls {
    flex-direction: column;
    gap: 0;
  }
}

.json-controls button,
.json-controls .import {
  flex: 1;
  background: linear-gradient(135deg, rgba(108, 99, 255, 0.25), rgba(0, 212, 255, 0.15));
  border: 1px solid rgba(108, 99, 255, 0.25);
  padding: 0.9rem;
  margin-bottom: 0.6rem;
  border-radius: 12px;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 600;
  color: #b0c0e0;
  font-size: 0.95rem;
  letter-spacing: 0.5px;
  animation: borderPulse 3s ease-in-out infinite;
  box-shadow: 0 2px 10px rgba(108, 99, 255, 0.15);
}

.json-controls button:hover,
.json-controls .import:hover {
  background: linear-gradient(135deg, rgba(108, 99, 255, 0.5), rgba(0, 212, 255, 0.3));
  border-color: rgba(0, 212, 255, 0.5);
  color: #fff;
  box-shadow: 0 4px 20px rgba(0, 212, 255, 0.5);
  transform: translateY(-2px) scale(1.02);
  animation: buttonGlow 1s ease-in-out infinite;
}

.json-controls .import {
  display: flex;
  align-items: center;
  justify-content: center;
}

.import input {
  display: none;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.wrong-answer-panel {
  background: linear-gradient(135deg, rgba(5, 6, 10, 0.98), rgba(3, 4, 8, 0.99));
  padding: 1.2rem;
  border-radius: 12px;
  border: 1px solid rgba(255, 77, 77, 0.25);
  margin: 1rem 0;
  animation: shake 0.5s ease, panelGlowError 2s ease-in-out infinite;
  box-shadow: 0 4px 20px rgba(255, 77, 77, 0.15);
  position: relative;
  z-index: 1;
}

@keyframes shake {
  0%, 100% { transform: translateX(0); }
  10%, 30%, 50%, 70%, 90% { transform: translateX(-5px); }
  20%, 40%, 60%, 80% { transform: translateX(5px); }
}

@keyframes panelGlowError {
  0%, 100% {
    box-shadow: 0 4px 20px rgba(255, 77, 77, 0.2);
    border-color: rgba(255, 77, 77, 0.3);
  }
  50% {
    box-shadow: 0 6px 30px rgba(255, 77, 77, 0.4);
    border-color: rgba(255, 77, 77, 0.5);
  }
}

.correct-answer {
  margin-bottom: 1rem;
  padding: 1rem;
  background: linear-gradient(135deg, rgba(0, 212, 255, 0.1), rgba(108, 99, 255, 0.1));
  border-left: 3px solid #00bfff;
  border-radius: 8px;
  color: #00bfff;
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 1.05rem;
  animation: answerReveal 0.6s ease;
}

@keyframes answerReveal {
  0% {
    opacity: 0;
    transform: translateY(-10px) scale(0.95);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.correct-answer strong {
  display: block;
  margin-bottom: 0.5rem;
  color: #c0d0ff;
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 600;
  animation: textPulse 1.5s ease-in-out infinite;
}

@keyframes textPulse {
  0%, 100% { opacity: 0.8; }
  50% { opacity: 1; }
}

.answer-comparison {
  margin-bottom: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.comparison-row {
  padding: 1rem;
  background: linear-gradient(135deg, rgba(108, 99, 255, 0.08), rgba(0, 212, 255, 0.08));
  border-radius: 8px;
  border: 1px solid rgba(108, 99, 255, 0.2);
}

.comparison-row strong {
  display: block;
  margin-bottom: 0.5rem;
  color: #c0d0ff;
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 600;
  font-size: 0.95rem;
}

.comparison-text {
  font-family: 'Courier New', monospace;
  font-size: 1.1rem;
  letter-spacing: 0.1em;
  word-break: break-all;
  color: #00bfff;
}

.comparison-text.correct {
  color: #00bfff;
  font-weight: 500;
}

.char {
  display: inline-block;
  padding: 0.2rem 0.3rem;
  border-radius: 3px;
  transition: all 0.3s ease;
  background: rgba(0, 212, 255, 0.1);
  border-bottom: 2px solid #00bfff;
}

.char.wrong {
  color: #ff4d4d;
  background: rgba(255, 77, 77, 0.2);
  border-bottom: 2px solid #ff4d4d;
  font-weight: 600;
  animation: errorPulse 1s ease infinite;
}

.char.missing {
  color: #ffa500;
  background: rgba(255, 165, 0, 0.2);
  border-bottom: 2px solid #ffa500;
}

@keyframes errorPulse {
  0%, 100% { 
    background: rgba(255, 77, 77, 0.2);
  }
  50% { 
    background: rgba(255, 77, 77, 0.4);
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateX(0) scale(1);
  }
}

.umlaut-buttons {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1rem;
  justify-content: center;
  flex-wrap: wrap;
  position: relative;
  z-index: 1;
}

@media (max-width: 480px) {
  .umlaut-buttons {
    gap: 0.4rem;
  }
}

.umlaut-btn {
  width: auto;
  padding: 0.6rem 0.9rem;
  margin: 0;
  background: rgba(5, 6, 12, 0.95);
  border: 1px solid rgba(108, 99, 255, 0.2);
  font-size: 1.1rem;
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 600;
  min-width: 45px;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.6);
  animation: umlautPulse 3s ease-in-out infinite;
}

@media (max-width: 480px) {
  .umlaut-btn {
    padding: 0.5rem 0.75rem;
    font-size: 1rem;
    min-width: 40px;
  }
}

@keyframes umlautPulse {
  0%, 100% {
    border-color: rgba(108, 99, 255, 0.25);
  }
  50% {
    border-color: rgba(108, 99, 255, 0.4);
  }
}

.umlaut-btn:hover:not(:disabled) {
  background: rgba(108, 99, 255, 0.3);
  border-color: rgba(0, 212, 255, 0.6);
  color: #00d4ff;
  box-shadow: 0 4px 15px rgba(0, 212, 255, 0.5);
  transform: translateY(-3px) scale(1.05);
  animation: umlautGlow 0.8s ease-in-out infinite;
}

@keyframes umlautGlow {
  0%, 100% {
    box-shadow: 0 4px 15px rgba(0, 212, 255, 0.5);
  }
  50% {
    box-shadow: 0 6px 25px rgba(0, 212, 255, 0.8);
  }
}

.umlaut-btn:disabled {
  opacity: 0.3;
  cursor: not-allowed;
  animation: none;
}
